.container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;

    @media (max-width: 600px) {
        height: 100%;
    }
    img{
        max-width: 100%;
        height: auto;
        max-height: 90%;
    }
}